  background-repeat: no-repeat;
  background-size: 100% 100%;
  font-family: "Raleway", sans-serif;
}

.header {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  display: flex;
  background: #fff;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  justify-content: space-between;
}

.header .logo {
  width: 150px;
  height: 60px;
  text-align: center;
  margin-left: 30px;
}

.header .logo img {
  height: 60px;
  padding: 15px;
}

.header .logo .beta {
  width: 35px;
  position: relative;
  margin-left: 115px;
  top: -43px;
}

.header .logo p {
  width: 30px;
  display: inline-block;
  color: #fff;
  text-align: center;
  font-size: 8px;
  padding: 2px;
  font-weight: 600;
  background: black;
  border-radius: 50px;
}

.action {
  width: 190px;
  height: 60px;
  padding: 17px;
  justify-self: flex-end;
  margin-right: 30px;
}

.action a {
  width: 70px;
  padding: 7px;
  display: inline-block;
  margin-right: 5px;
  font-size: 12px;
  color: #000;
  text-align: center;
  border-radius: 50px;
