}

.action a:nth-child(2) {
  color: #fff;
  background: #4021d3;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
}

.action a:nth-child(2):hover {
  background-color: #5139ca;
}

.section {
  border-top: 1px solid white;
  margin: 0 14.4%;
  height: 90%;
  padding-top: 50px;
}

.section h1 {
  font-size: 48px;
