@-webkit-keyframes heading {
  0% {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(xx) translateY(0);
            transform: rotate(xx) translateY(0);
    opacity: 1;
  }
}

@keyframes heading {
